*{
    background:rgba(149, 150, 220, 0.225);
    margin:0;
    padding:0;
    box-sizing:border-box;
   font-family: "Poppins",sans-serif;
}

.mainapp{
    display:flex;
    flex-direction:column;
    justify-content:center;
    align-items:center;
}

h2{
    margin:4px 0;
    padding:0;
    
}
p{
    margin:4px 0;
    padding:0;
}
span{
    font-weight:400;
    color:rgba(128, 128, 128, 0.689);
}
.card h2 ,p ,span,.card-button,a{
    background-color:white;
    text-decoration: none;
}
.card{
    border-radius:5px;
    box-shadow: 0 0 4px 0 #988f8f94;
    margin:11px 0;
    /* height:130px; */
    display:flex;
    flex-direction:column;
    justify-content:space-between;
    /* align-items:center; */
    border:1px solid gray;
    padding:8px;
    width:60%;
    background-color:white;
}
.card-button{
    display:flex;
    justify-content:space-between;
    align-items:center;
    margin: 11px 7px
}
.card-button :last-child{
    color:rgba(255, 0, 0, 0.679);
    font-size:12px;
    font-weight: bold;
}
.card-button :first-child{
    color: #0000ffa1;
    font-size:12px;
    font-weight: bold;
}
.searchflex{
display:flex;
flex-direction:column;
justify-content:center;
align-items:center;
margin:10px 0;
background:transparent;
}
.searchflex h1{
    margin:16px 0;
    background:transparent;
}
.pagination_btn{
    display:flex;
align-items:center;
background:transparent;

}
.pagination_btn button{
    margin:16px 9px;
    background:rgba(9, 9, 9, 0.958);
    color:white;
    padding:5px;
    cursor: pointer;

}
.pagination_btn p{
    background:transparent;
    color:rgb(98, 97, 97);
}
@media(max-width:392px){
    .searchflex h1{
        font-size:29px
    }
}